*{
    margin: 0;
    padding: 0;
}
.name{
 color: white;
 font-style: italic;
 
}

.des{
    color: aliceblue;
}
.content{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 31vh;
}
.pic{
    border: 1px solid black;
    border-radius: 518px;
    height: 17vh;
}
.pic:hover{
    height: 300px;
    
    
}
body{
    background-color:#1f374a;
    color: white;
}
.social-link{
    height: 4vh;
    margin-right: 9px;
}
.social-link:hover{
    transform: scale(2);
    margin-right: 20px;
    margin-left: 10px;
}
.background-clip{
    position: absolute;
    
    right: 0;
    bottom: 0;
    z-index: -1;
}
@media (min-aspect-ratio:16/9) {
    .background-clip{
        width: 100%;
        height: auto;
    }
}
@media (max-aspect-ratio:16/9) {
    .background-clip{
        width: auto;
        height: 100%;
    }
}
@media (max-width:750px) {
    .background-clip{
        height: 100%;
    }
}
@media (width:1000px) {

    .content{
        width: 30dvh;
    }
    
}
